/*onclick="history.back()"*/
.HomeNavbar_boxings__3XD_I>div{
  cursor: pointer;
}
.HomeNavbar_boxings__3XD_I>div:nth-child(1)>img{
  position: relative;
  left: 30%;
  top:25%;
  transform: rotateY(180deg);
}
.HomeNavbar_boxings__3XD_I>div:nth-child(2)>img{
  position: relative;
  left: 35%;
  top:25%;
}
.HomeNavbar_boxings__3XD_I>div:nth-child(1){
  width: 32px;
  height: 32px;
  background-color: #181818;
  margin-right: 20px;
  border-radius: 16px;
}
.HomeNavbar_boxings__3XD_I>div:nth-child(2){
  width: 32px;
  border-radius: 16px;
  height: 32px;
  background-color: #181818;
}
